import React from "react";

export default function ConfirmDeleteModal({ note, onCancel, onConfirm }) {
  if (!note) return null;

  return (
    <div className="modalBackdrop" onClick={onCancel}>
      <div className="confirmModal" onClick={(e) => e.stopPropagation()}>
        <button className="confirmClose" onClick={onCancel} aria-label="Zamknij">
          
        </button>

        <div className="confirmIconWrap">
          <div className="confirmIcon"></div>
        </div>

        <div className="confirmTitle">Czy jeste pewien?</div>
        <div className="confirmText">
          Czy na pewno chcesz usun notatk?
          <br />
          Tego procesu nie da si odwrci.
        </div>

        <div className="confirmActions">
          <button className="confirmCancel btnSlide" onClick={onCancel}>
            Anuluj
          </button>
          <button className="confirmDelete btnSlide" onClick={() => onConfirm(note)}>
            Tak, Usu
          </button>
        </div>
      </div>
    </div>
  );
}
