import React, { useState } from 'react';
import '../css/Registration.css';
import '../css/LoginPage.css';
import { Link } from 'react-router-dom';

function RegistrationPage() {
  const [username, setUsername] = useState('');
  const [password, setPassword] = useState('');
  const [confirmPassword, setConfirmPassword] = useState('');
  const [firstName, setFirstName] = useState('');
  const [lastName, setLastName] = useState('');
  const [email, setEmail] = useState('');
  const [registered, setRegistered] = useState(false);

  const handleRegistration = (event) => {
    event.preventDefault();
    if (username && password && password === confirmPassword && firstName && lastName && email) {
      // Tutaj mona doda logik rejestracji uytkownika
      // Na potrzeby tego przykadu przyjmujemy, e uytkownik zostanie zarejestrowany, jeli poda nazw uytkownika, haso, imi, nazwisko, email oraz potwierdzi haso
      setRegistered(true);
      alert('Zarejestrowano pomylnie!');
    } else {
      alert('Prosz poda wszystkie wymagane dane poprawnie.');
    }
  };

  return (
    <div id='unique-container'>
    <div className="registration-container">
      {registered ? (
        <div className="registration-success">
          <h2>Rejestracja zakoczona pomylnie!</h2>
          <p>Moesz teraz przej do strony logowania.</p>
          <p><Link className={'link-style-register'} to="/"> Zaloguj si</Link></p>
        </div>
      ) : (
        <div className="registration-form login-animation">
          <h2>Rejestracja</h2>
          <form onSubmit={handleRegistration}>
            <div className="form-group">
              <label>Imi</label>
              <input
                type="text"
                value={firstName}
                onChange={(e) => setFirstName(e.target.value)}
              />
            </div>
            <div className="form-group">
              <label>Nazwisko</label>
              <input
                type="text"
                value={lastName}
                onChange={(e) => setLastName(e.target.value)}
              />
            </div>
            <div className="form-group">
              <label>E-mail</label>
              <input
                type="email"
                value={email}
                onChange={(e) => setEmail(e.target.value)}
              />
            </div>
            <div className="form-group">
              <label>Nazwa uytkownika</label>
              <input
                type="text"
                value={username}
                onChange={(e) => setUsername(e.target.value)}
              />
            </div>
            <div className="form-group">
              <label>Haso</label>
              <input
                type="password"
                value={password}
                onChange={(e) => setPassword(e.target.value)}
              />
            </div>
            <div className="form-group">
              <label>Potwierd haso</label>
              <input
                type="password"
                value={confirmPassword}
                onChange={(e) => setConfirmPassword(e.target.value)}
              />
            </div>
            <button type="submit" className='register-button'>Zarejestruj Si</button>
            <p>Masz ju konto?<Link className={'link-style-register'} to="/"> Zaloguj si</Link></p>
          </form>
        </div>
      )}
    </div>
    </div>
  );
}


export default RegistrationPage;
